import React from "react";
import { FiStar } from "react-icons/fi";
import { FaWindowClose } from "react-icons/fa";
import {
  AiFillAliwangwang,
  AiFillCloseCircle,
  AiFillLock,
  AiFillGoogleCircle,
  AiFillMobile,
  AiOutlineAlignLeft,
} from "react-icons/ai";
import axios from "axios";
import swal from "sweetalert";

export default function Verify(props) {
  const [password, set_password] = React.useState("");
  const [cpassword, set_cpassword] = React.useState("");

  const passwords = async () => {
    if (password != "" && cpassword != "") {
      if (password == cpassword) {
        let res = await axios.post(
          "/user/verify/forgot/password",
          {
            token: props.match.params.token,
            master_password: props.match.params.id,
            new_password: password,
            confirm_password: cpassword,
          }
        );
        if (res.status == 200) {
          swal("Password Changed.");
          window.location.href = "/";
        } else {
          swal("please try again");
        }
        console.log(res);
      } else {
        swal("Password Must be Same.");
      }
    } else {
      swal("Please Enter Password.");
    }
  };

  return (
    <div>
      {/* <p>{props.match.params.token}</p>
        <p>{props.match.params.id}</p> */}
      <div className="user_card container">
        <div className="d-flex justify-content-center">
          <div className="brand_logo_container">
            {/* <img src="./images/logo.png" className="img-m img-mm" /> */}
            <span style={{ color: "white", fontSize: 20 }}>Reset Password</span>
          </div>
        </div>
        <div className="d-flex justify-content-center form_container">
          <form
            onSubmit={(e) => {
              e.preventDefault();
            }}
          >
            <div className="input-group mb-3">
              <div className="input-group-append">
                <span className="input-group-text">
                  <AiFillLock size={20} color="white" />
                </span>
              </div>
              <input
                type="password"
                className="mt-3"
                onChange={(e) => {
                  set_cpassword(e.target.value);
                }}
                className="form-control"
                placeholder="password"
              />
            </div>

            <div className="input-group mb-2">
              <div className="input-group-append">
                <span className="input-group-text">
                  <AiFillLock size={20} color={"white"} />
                </span>
              </div>
              <input
                type="password"
                className="mt-3"
                onChange={(e) => {
                  set_password(e.target.value);
                }}
                className="form-control"
                placeholder="password"
              />
            </div>

            <div className="d-flex justify-content-center mt-3 login_container">
              <button
                type="button"
                name="button"
                className="btn login_btn"
                onClick={() => {
                  passwords();
                }}
              >
                Reset Password
              </button>
            </div>
          </form>
        </div>
        <span
          style={{ textAlign: "center", fontSize: "12px", color: "red" }}
          id="err"
          className="d-none"
        >
          Enter Email to Continue
        </span>
        <div className="d-flex justify-content-center links"></div>
      </div>
    </div>
  );
}
