import React from 'react';
import axios from "axios";
import swal from "sweetalert";
import { useHistory} from "react-router-dom";

export default function Reset() {

    const history = useHistory();
    const [new_password, set_new_password] = React.useState("");
    const [old_password, set_old_password] = React.useState("");


    const Resetpassword = async () => {
      //  alert("called")
        try {
          // alert("calledtry")
          console.log("passowrds",new_password,old_password)
          if(new_password != "" && old_password != "" )
          {
            const response = await axios.post("/admin/reset/password",
              {
                  old_password:old_password,
                  new_password:new_password,
              },
               {headers: {'Authorization': 'Bearer '+localStorage.getItem("admin")}}
            );
      
            console.log("***", response);
            swal("","Password Reset Successfully","success");
            history.push("/admin/home")
          }
          else{
            swal("","Please fill all details","error");
          }
        }
        catch (error) {
          alert("calledcatch")
          console.error("-------", error);
          swal("","Password Reset Failed","error");
        }
      }

      ///////////////////////////////
    return (
        <div class="container" style={{display:"flex",justifyContent:"center",marginTop:"250px"}}>
            <div>
            <label class="text-primary"><h2>Reset Password</h2></label>
            <div class="form-group">
                <label>OLD Password</label>
                <input type="text"
                className="form-control"
                placeholder="Enter old password" 
                style={{width:"300px"}}
                value={old_password}
                onChange={event => {set_old_password(event.target.value)}}
                />
               
            </div>
            <div class="form-group">
                <label>NEW Password</label>
                <input type="text" 
                className="form-control" 
                placeholder="Enter new password" 
                style={{width:"300px"}} 
                value={new_password}
                onChange={event => {set_new_password(event.target.value)}}
                />
            </div>
    
            <button class="btn btn-primary" style={{width:"300px"}} onClick={() => {Resetpassword()}} >Save</button>
            </div>
        </div>
    );
}

