import React from "react";
import {
  Route,
  Switch,
  Link,
  useHistory,
  BrowserRouter as Router,
} from "react-router-dom";

import MainPage from "./pages/mainpage";
import Search from "./pages/search";
import Login from "./pages/login";
import Signup from "./pages/signup";
import Room_details from "./pages/Room_details"
import Booking from "./pages/Booking"
import Edit from "./pages/Edit"
import Reset from "./pages/Reset"



import AdminDashboard from "./pages/AdminDashboard"
import Verify from "./pages/verify";
export default function Routers() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route exact path="/" component={MainPage} />
          <Route exact path="/search" component={Search} />
          <Route exact path="/admin/home" component={AdminDashboard} />
          <Route exact path="/admin" component={Login} />
          <Route exact path="/admin/room_details" component={Room_details} />
          <Route exact path="/Booking" component={Booking} />
          <Route exact path="/edit" component={Edit} />
          <Route exact path="/resetpassword" component={Reset} />
          <Route exact path="/verify/:token/:id" component={Verify} />
        </Switch>
      </Router>
    </div>
  );
}
