import axios from 'axios';
import React from 'react';
import {ImCross} from "react-icons/im"
import PulseLoader from "react-spinners/PulseLoader";
// Can be a string as well. Need to ensure each key-value pair ends with ;
const override = `
  display: block;
  margin: 0 auto;
  border-color: red;
`;

const Photo_upload = () => {

    const [Image,set_images]=React.useState([])
    const [fileObj,set_file]=React.useState([])
    const [uplaoded,set_uploaded_images]=React.useState([])
    let [loading, setLoading] = React.useState(false);

            function uploadMultipleFiles(e) {
                let fileObjs=[]
                let fileArray=[]
                fileObjs.push(e.target.files);
                set_images(e.target.files)
               
                for (let i = 0; i < fileObjs[0].length; i++) {
                    fileArray.push(URL.createObjectURL(fileObjs[0][i]));
                }
                set_file(fileArray);
                
            }

                    React.useEffect(()=>{
                        get_all_images()
                    },[])



            const get_all_images =async ()=>{
                axios.get('/site/get/site_banner').then((res)=>{
                    if(res.data.length!=0){
                        // let a =[]
                        // for(let i=0;res.data.length;i++){
                        //     a.push(JSON.parse(res.data[i].image));
                        // }
                        set_uploaded_images(res.data)
                        console.log(res.data)
                    }
                })
            }

            const delete_images = async (index)=>{
                axios.post('/site/delete/site_banner',{id:index}).then((res)=>{
                    window.location.reload();
                })
            }

            const  set_banner = async () => {
                console.log(Image)
                let img = new FormData()
                for(let i = 0; i < Image.length; i++ ){
                     img.append("image",Image[i])
                }
                console.log(img)
                try {
                    setLoading(true);
                    const response = await axios.post("/site/add/site_banner",
                    img
                    );
                    console.log("***",response);
                    setLoading(false);
                    window.location.reload();
                    
                }
                catch (error) {
                    console.error("-------",error);
                }
            }

            

    return ( 
        <div>
        <h3>Banner Images</h3>
        <input type="file" onChange={uploadMultipleFiles} multiple />
        
        {fileObj.map((data)=>{
            return(
            <span>
                <img src={data} className="img_bob" />
                {/* <ImCross /> */}
            </span>
            )
        })}

        <h5>Upload Images</h5>

        {uplaoded.map((data)=>{
           
            return<span><img className="img_bob" src={data.image} /><ImCross onClick={() => {delete_images(data.id)}} /></span>

        })}
        <br></br>
        <br></br>
        <div>
            {
                loading == true?            
                 <PulseLoader color="green" loading={loading} css={override} size={15} />
            :
                    <button className="btn btn-primary" onClick={()=>{set_banner()}}>Update Banner</button>    
            }
            
        </div>
        
        
        </div>
     );
}
 
export default Photo_upload;