import axios from "axios";
import React from "react";
import { AiFillCheckCircle, AiTwotoneEdit ,AiFillDelete} from "react-icons/ai";
import { useHistory } from "react-router-dom";
import swal from "sweetalert"
import Moment from "moment"
import { BiBed } from "react-icons/bi";
import $ from "jquery"
import StripeCheckout from "react-stripe-checkout";
import Button from 'react-bootstrap/Button'
import Modal from 'react-bootstrap/Modal'
import Form from 'react-bootstrap/Form'

export default function AdminDashboard() {
  const [type, set_type] = React.useState("");

  const [images, setImage] = React.useState("");
  const [name, set_name] = React.useState("");
  const [person_in_room, set_person] = React.useState("");
  const [price, set_price] = React.useState("");
  const [description, set_description] = React.useState("");
  const [aminities, set_aminities] = React.useState("TV, Fridge, Microwave, Iron, Iron Board, Hair Dryer, Coffee Maker, Parking, Pets allowed, AAA Approved, High Internet");
  const [room, set_room] = React.useState("");

  const [data, set_data] = React.useState([]);
  const [rprice,realprice]=React.useState("")

  const [show, setShow] = React.useState(false);

  const [noofrooms, set_noofrooms] = React.useState("");
  const [roomprice, set_roomprice] = React.useState("");
  const [rdiscount, set_rdiscount] = React.useState("");
  const [rid, set_rid] = React.useState("");

  const handleClose = () => {
    setShow(false);
  }
  
  const handleShow = (data) => {
    set_noofrooms(data.no_of_rooms);
    set_roomprice(data.amount);
    set_rdiscount(data.discount);
    set_rid(data.id);
    setShow(true);
  } 

  const history = useHistory();

  const EditRoom = async () => {
    let res = await axios.put("/admin/update/room", {
      amount: roomprice,
      discount: rdiscount,
      no_of_rooms: noofrooms,
      room_id: rid,
    },
    {headers: {'Authorization': 'Bearer '+localStorage.getItem("admin")}}
    );
    console.log(res.data);
    window.location.reload();
  };

  const called_api = async (types) => {
    let res;
    console.log(types)
    if (types == "roomlist") {
      console.log("room list");
      res = await axios.get("/admin/list/room", {
        headers: { Authorization: "Bearer " + localStorage.getItem("admin") },
      });
      console.log(res);
      set_data(res.data.data);
    }
    else if (types == "user list") {
      res = await axios.get("/admin/list/user", {
        headers: { Authorization: "Bearer " + localStorage.getItem("admin") },
      });
      console.log(res);
      set_data(res.data.data);

    }
    else{
      // /admin/bookList
      res = await axios.get("/admin/bookList", {
        headers: { Authorization: "Bearer " + localStorage.getItem("admin") },
      });
      console.log(res);
      set_data(res.data.data);
    }
  };

  React.useState(() => {
    called_api();
  }, [type]);

  $(document).ready(function () {
    var numitems = $("#myList li").length;
    $("ul#myList").css("column-count", Math.round(numitems / 2));
  });

  //     amenities: "wifi, gaming"
  // amount: "5000"
  // description: "ac, pool"
  // id: 1
  // media: "https://heritagehotel.s3.ap-south-1.amazonaws.com/1618290341431flower.jpg"
  // no_of_person: "2"
  // no_of_rooms: 0
  // room_name: "deluxe"
  // status: "active"

  const create = async () => {
      let form = new FormData()
      form.append("room_name",name)
      form.append("description",description)
      form.append("amount",price)
      form.append("amenities",aminities)
      form.append("media",images)
      form.append("no_of_person",person_in_room)
      // form.append("media",images)
      form.append("no_of_rooms",room)
      form.append("discount",rprice)
    //   {
    //     room_name: name,
    //     description: description,
    //     amount: price,
    //     amenities: aminities,
    //     media: images,
    //     no_of_person: person_in_room,
    //     no_of_rooms:room
    //   }
    let res = await axios.post(
      "/admin/create/room",form
      ,
      { headers: { Authorization: "Bearer " + localStorage.getItem("admin") } }
    );
        if(res.status==200){
            swal("done")
        }
  };

  

  const handleDEL = async (delid) => {

    try {
      const response = await axios.post("/admin/delete/room",
        {
          id:delid,
        },
       
      );

      console.log("***", response.data);
      window.location.reload();
    }
    catch (error) {
      console.error("-------", error);
    }
  }
  return (
    <>
      <div className="container py-5">
      <button
          className="btn btn-primary mx-5"
          onClick={() => {
            set_type("");
           
          }}
        >
          Booking
        </button>
        <button
          className="btn btn-primary mx-4"
          onClick={() => {
            set_type("roomlist");
            called_api("roomlist");
          }}
        >
          List of Room
        </button>
        <button
          className="btn btn-primary mx-5"
          onClick={() => {
            set_type("create room");
          }}
        >
          Create Room
        </button>
        <button
          className="btn btn-primary mx-5"
          onClick={() => {
            set_type("user list");
            called_api("user list");
          }}
        >
          User List
        </button>
        <button
          className="btn btn-primary mx-4"
          onClick={() => {
            history.push("/edit")
          }}
        >
          Edit Site
        </button>
        <button
          className="btn btn-primary mx-5"
          onClick={() => {
            history.push("/resetpassword")
          }}
        >
          Reset Password
        </button>


        {type == "" ? 
        <div style={{marginTop:30}}>
          <span style={{fontSize:12,padding:20}}>Booking List</span>
          <table className="table table-border" style={{fontSize:12,marginTop:20}}>
          <tr className="bg-dark text-white"><td>Customer Name</td> <td>Room</td> <td>Check In -- Check Out</td> <td>status</td></tr>

          {data.map((data,i)=>{
            return(
              <tr style={{backgroundColor:data.status=="booked"?"#e8faea":"#f5f5f5"}}>
                <td>{data.name}</td>
                <td>{data.room_name}</td>
                <td>{Moment(data.start_date).format("MMM Do YY")+"--"+Moment(data.end_date).format("MMM Do YY")}</td>
                <td>{data.status}</td>
              </tr>
            )
          })}

        </table>
        </div> : type == "roomlist" ? (
          <div style={{marginTop:30}}>
          <span style={{fontSize:12,padding:20}}>Room List</span>
          <table className="table table-border" style={{fontSize:12,marginTop:20}}>
            <tr>
              <td>Name</td>
              <td>Price</td>
              <td>Discount</td>
              <td>Persons</td>
              <td>Rooms For Booking</td>
              <td>Status</td>
              <td>Action</td>
            </tr>
            {data.map((data, i) => {
              return (
                <tr>
                  <td>{data.room_name}</td>
                  <td>{data.amount}</td>
                  <td>{data.discount}</td>
                  <td>{data.no_of_person}</td>
                  <td>{data.no_of_rooms}</td>
                  <td>
                    {data.status == "active" ? (
                      <span style={{ color: "green" }}>Active</span>
                    ) : (
                      <span style={{ color: "red" }}>InActive</span>
                    )}
                  </td>
                  <td>
                    <span>
                      <AiTwotoneEdit size={15} color="grey" onClick={() => {handleShow(data)}} />
                      <AiFillDelete size={15} color="grey" style={{marginLeft:"10px"}} onClick={() => {handleDEL(data.id)}} />
                    </span>
                    </td>
                  
                </tr>
              );
            })}
          </table>
                <Modal show={show} onHide={handleClose}>
                  <Modal.Header closeButton>
                    <Modal.Title>Edit Room</Modal.Title>
                  </Modal.Header>
                  <Modal.Body>

                  <Form>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Label>No. of Rooms</Form.Label>
                    <Form.Control
                     type="text" 
                     placeholder="Number of rooms"
                     value={noofrooms}
                    onChange={(e) => {
                      set_noofrooms(e.target.value);
                    }}
                     
                      />
                    
                  </Form.Group>

                  <Form.Group controlId="formBasicPassword">
                    <Form.Label>Price</Form.Label>
                    <Form.Control 
                    type="text" 
                    placeholder="price"
                    value={roomprice}
                     onChange={(e) => {
                       set_roomprice(e.target.value);
                     }}
                    
                     />
                  </Form.Group>

                  <Form.Group controlId="formBasicPassword">
                    <Form.Label>Discount</Form.Label>
                    <Form.Control 
                    type="text" 
                    placeholder="discount"
                    value={rdiscount}
                    onChange={(e) => {
                    set_rdiscount(e.target.value);
                  }}
                    />
                  </Form.Group>
                  
                  </Form>
                 

                  </Modal.Body>
                  <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                      Close
                    </Button>
                    <Button variant="primary" onClick={() => EditRoom()}>
                      Save Changes
                    </Button>
                  </Modal.Footer>
                </Modal>
          </div>
        ) : type == "create room" ? (
          <div style={{marginTop:30}}>
          <span style={{fontSize:12,padding:20,fontWeight:"bold"}}>Create List</span>
          <div style={{flexDirection:"row"}}>
          <table className="table table-borderless col-8" style={{float:"left"}}>
            <tr>
              <td>Image</td>
              <td>
                <input
                  className="form-control"
                  type="file"
                  onChange={(e) => {
                    setImage(e.target.files[0]);
                  }}
                  single
                />
              </td>
            </tr>
            {/* <tr>
              {images == "" ? null : (
                <td>
                  <img
                    style={{ width: 100, height: 100, marginLeft: "100%" }}
                    src={URL.createObjectURL(images)}
                  />
                </td>
              )}
            </tr> */}
            <tr>
              <td>Name of Room</td>
              <td>
                <input
                  className="form-control"
                  type="text"
                  onChange={(e) => {
                    set_name(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>No. of Room</td>
              <td>
                <input
                className="form-control"
                  type="text"
                  onChange={(e) => {
                    set_room(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>No of person allowed</td>
              <td>
                <input
                className="form-control"
                  type="text"
                  onChange={(e) => {
                    set_person(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>Amount</td>
              <td>
                <input
                  type="text"
                  className="form-control"
                  onChange={(e) => {
                    realprice(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>Discount Amount</td>
              <td>
                <input
                  type="text"
                  className="form-control"
                  onChange={(e) => {
                    set_price(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>Description</td>
              <td>
                <input
                  type="text"
                  className="form-control"
                  onChange={(e) => {
                    set_description(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>Amenities</td>
              <td>
                <input
                  type="text"
                  className="form-control"
                  value={aminities}
                  onChange={(e) => {
                    set_aminities(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td></td>
              <button
                class="btn btn-warning mt-5"
                onClick={() => {
                  create();
                }}
              >
                Create Room
              </button>
            </tr>
          </table>

                {/* create room car */}
                <div className="col-md-4" style={{float:"left"}}>
                    <div className="card card-cascade card-ecommerce wider shadow mb-5 ">
                      <div className="view view-cascade overlay text-center">
                        <img className="card-img-top" src={images==""?null:window.webkitURL.createObjectURL(images)} alt="" />
                        <a>
                          <div className="mask rgba-white-slight"></div>
                        </a>{" "}
                      </div>
                      <div className="card-body card-body-cascade">
                        <h4 className="card-title">
                          <strong style={{ textAlign: "left" }}>
                            
                            <a>{name}</a>
                          </strong>
                        </h4>
           
                        <div
                          style={{
                            flexDirection: "row",
                            position: "absolute",
                            right: 20,
                          }}
                        >
                          <span>
                            <BiBed size={15} color="grey" />
                            {"     "}
                            {person_in_room}
                          </span>
                          
                        </div>

                        <div className="col">
                          <p className="card-text">{description}</p>
                        </div>
                        <hr />
                        <span>Amenities</span>
                        <div style={{ borderWidth: 1 }}>
                          <ul className="uloo" style={{maxWidth: 250}} >
                            {aminities.split(",").map((data) => {
                              return (
                                <span className="lioo">
                                  {"✓  "}
                                  {data}
                                </span>
                              );
                            })}
                          </ul>


                          <span style={{color:"red",fontSize:10}}>Room Left {room}</span>
                          <span style={{color:"red",fontSize:10,display:"flex",float:"right"}}>Pay at Hotel</span>
                        </div>

                        <div className="card-footer">
                        <span style={{ color: "red", fontWeight: "bold",textDecoration:"line-through",fontSize:13 }}>
                            {" $ "} {rprice}
                          </span>
                          <span style={{ color: "green", fontWeight: "bold" }}>
                            {" $ "} {price}
                          </span>
                          <div
                            style={{
                              position: "absolute",
                              right: 30,
                              bottom: 20,
                            }}
                          >
                              {/* <StripeCheckout
                                
                              /> */}
                          <button
                                class="btn btn-warning font-weight-bold"
                              >
                                 Book Now
                              </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

          </div>
         
          </div>
        
        
        ) : type == "user list" ? (
          <div style={{marginTop:30}}>
          <span style={{fontSize:12,padding:20}}>User List</span>
          <table className="table table-border" style={{fontSize:12,marginTop:20}}>
            <tr>
              <td>Id</td>
              <td>Name</td>
              <td>mobile</td>
              <td>email</td>
            </tr>
            {data.map((data, i) => {
              return (
                <tr>
                  <td>{data.id}</td>
                  <td>{data.name}</td>
                  <td>{data.mobile}</td>
                  <td>{data.email}</td>
                </tr>
              );
            })}
          </table>
          </div>
        ) : null}
      </div>
    </>
  );
}
