import React from "react";
import axios from "axios";
import swal from "sweetalert";
import Photo_upload from "./Photo_upload";


export default function Edit() {
  const [about, set_about] = React.useState("");
 

  const get_about = async () => {
    let res = await axios.post("/site/about", {});
    
    set_about(res.data[0].about);
    
  };

  React.useEffect(() => {
    get_about();
    getContact();
    getAmenities();
    getFaq();
    getPolicy();
    getFacilities();
  }, []);


  const [phone, set_phone] = React.useState("");
  const [fax, set_fax] = React.useState("");
  const [email, set_email] = React.useState("");
  const [question, set_question] = React.useState("");
  const [answer, set_answer] = React.useState("");
  const [heading, set_heading] = React.useState("");
  const [description, set_description] = React.useState("");
  const [amenities, set_amenities] = React.useState("");
  // const [pol_id, set_pol_id] = React.useState("");
  // const [ame_id, set_ame_id] = React.useState("");
  const [fac_id, set_fac_id] = React.useState("");
  const [fac_heading, set_fac_heading] = React.useState("");
  const [fac_description, set_fac_description] = React.useState("");
  const [file, set_file] = React.useState("");
  const [ame, set_ame] = React.useState([]);
  const [faq, set_faq] = React.useState([]);
  const [pol, set_pol] = React.useState([]);
  const [fac, set_fac] = React.useState([]);


  ///////////////////contact start
  const getContact = async () => {
   
		try {
          const response = await axios.get("/site/get/contactUs",
          {
              
          },
          // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
          );
      
             
             set_phone(response.data[0].phone);
             set_fax(response.data[0].fax);
             set_email(response.data[0].email);
            // set_data(response.data)
            console.log("***",response.data[0]);
        }
        catch (error) {
          console.error("-------",error);
        }
	  }

  const updateContact = async () => {
   
		try {
          const response = await axios.put("/site/update/contactUs",
          {
              id:1,
              phone:phone,
              fax:fax,
              email:email,
          },
          // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
          );
      
             //set_con(response.data[0]);
            // set_data(response.data)
            window.location.reload();
            console.log("***",response);
        }
        catch (error) {
          console.error("-------",error);
        }
	  }

    /////////////////////////contact end

    ///////////////////////////////////facilities api start
  const getFacilities = async () => {
   
		try {
          const response = await axios.get("/site/get/facilities",
          {
              
          },
          // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
          );
      
             set_fac(response.data);
            // set_data(response.data)
            console.log("***",response.data);
        }
        catch (error) {
          console.error("-------",error);
        }
	  }

    console.log(fac_heading);
    console.log(fac_description);
    console.log(file);

    const addFacilities = async () => {
      
     
      let faci= new FormData()
      faci.append("heading",fac_heading)
      faci.append("description",fac_description)
      faci.append("images",file)

      console.log("iuytfdfghj",faci)
      
      try {
            const response = await axios.post("/site/add/facilities",            
                faci
            );
        
              //  set_fac(response.data);
              // set_data(response.data)
              window.location.reload();
              console.log("***",response.data);
          }
          catch (error) {
            console.error("-------",error);
          }
      }

      const delFacilities = async (fac_id) => {
      
        
        try {
              const response = await axios.post("/site/delete/facilities",
              {
                  id:fac_id
              },
              // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
              );
          
                //  set_fac(response.data);
                // set_data(response.data)
                window.location.reload();
                console.log("***",response.data);
            }
            catch (error) {
              console.error("-------",error);
            }
        }

      const updateFacilities = async (fac_id) => {
      
     console.log(fac_id)
        let faci= new FormData()
        faci.append("id",fac_id)
        faci.append("heading",fac_heading)
        faci.append("description",fac_description)
        faci.append("images",file)
        
        try {
              const response = await axios.put("/site/update/facilities",
                  faci
              );
          
                //  set_fac(response.data);
                // set_data(response.data)
                window.location.reload();
                console.log("***",response.data);
            }
            catch (error) {
              console.error("-------",error);
            }
        }
     ///////////////////////////////////facilities api ends 

    ///////////////////////////////////roomamenities api start
    

     const getAmenities = async () => {
   
      try {
            const response = await axios.get(" /site/get/RoomAmenities",
            {
                
            },
            // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
            );
        
            set_ame(response.data);
              // set_data(response.data)
              console.log("***",response.data);
          }
          catch (error) {
            console.error("-------",error);
          }
      }

      const addAmenities = async () => {
   
        try {
              const response = await axios.post("/site/add/RoomAmenities",
              {
                amenities:amenities,
              },
              // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
              );
          
              // set_ame(response.data);
                // set_data(response.data)
                window.location.reload();
                console.log("***",response);
            }
            catch (error) {
              console.error("-------",error);
            }
        }

        const delAmenities = async (ame_id) => {
          console.log(ame_id)
   
          try {
                const response = await axios.post("/site/delete/RoomAmenities",
                {
                  id:ame_id,
                },
                // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
                );
            
                // set_ame(response.data);
                  // set_data(response.data)
                  window.location.reload();
                  console.log("***",response);
              }
              catch (error) {
                console.error("-------",error);
              }
          }
       
    ///////////////////////////////////roomamenities api end

/////////////////////////////////////////faq api start

    const getFaq = async () => {
   
      try {
            const response = await axios.get("/site/get/faq",
            {
                
            },
            // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
            );
        
            set_faq(response.data);
            // set_question(response.data.question);
            // set_answer(response.dataanswer);
              // set_data(response.data)
              console.log("***",response.data);
          }
          catch (error) {
            console.error("-------",error);
          }
      }

      // const updateFaq = async () => {
   
      //   try {
      //         const response = await axios.put("/site/update/faq",
      //         {
      //           id:id,
      //           question:question,
      //           answer:answer,   
      //         },
      //         // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
      //         );
          
      //         set_faq(response.data);
      //         // set_question(response.data.question);
      //         // set_answer(response.dataanswer);
      //           // set_data(response.data)
      //           console.log("***",response.data);
      //       }
      //       catch (error) {
      //         console.error("-------",error);
      //       }
      //   }

        const addFaq = async () => {
   
          try {
                const response = await axios.post("/site/addFaq",
                {
                
                  question:question,
                  answer:answer,   
                },
                // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
                );
            
                window.location.reload();
                  console.log("***",response);
              }
              catch (error) {
                console.error("-------",error);
              }
          }

          const delFaq = async (faq_id) => {
   
            try {
                  const response = await axios.post("/site/delete/faq",
                  {
                      id:faq_id,
                  },
                  // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
                  );
              
                  window.location.reload();
                    console.log("***",response.data);
                }
                catch (error) {
                  console.error("-------",error);
                }
            }
      


/////////////////////////////////////////faq api ends


/////////////////////////////////////////policy api start

      const getPolicy = async () => {
 
        try {
              const response = await axios.get("/site/get/policy",
              {
                  
              },
              // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
              );
          
              set_pol(response.data);
                // set_data(response.data)
                console.log("***",response.data);
            }
            catch (error) {
              console.error("-------",error);
            }
        }

        const addPolicy = async () => {
 
          try {
                const response = await axios.post("/site/add/policy",
                {
                  heading:heading,
                  description:description,
                },
                // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
                );
            
                // set_pol(response.data);
                  // set_data(response.data)
                  window.location.reload();
                  console.log("***",response.data);
              }
              catch (error) {
                console.error("-------",error);
              }
          }

          const delPolicy = async (pol_id) => {
 
            try {
                  const response = await axios.post("/site/delete/policy",
                  {
                   id:pol_id,
                  },
                  // {headers: {'Authorization': 'Bearer '+localStorage.getItem("token")}}
                  );
              
                  // set_pol(response.data);
                    // set_data(response.data)
                    window.location.reload();
                    console.log("***",response.data);
                }
                catch (error) {
                  console.error("-------",error);
                }
            }

/////////////////////////////////////////policy api end

/////////////////////////////////save submit and update

  const save=async()=>{
      
      let res2 = await axios.put("/site/update/about",{
          id:1,
          about:about
      })
      if(res2.status ==200){
          swal("Site data Updated.")
      }
  }

const HandleClick = (data) => {
    set_fac_heading(data.heading);
    set_fac_description(data.description);
    set_file(data.images);
    set_fac_id(data.id);
}


  return (
    <>
    <span style={{textAlign:"center",position:"absolute",top:30,right:0,left:0,fontWeight:"bold",fontSize:20}}>Edit You Site Details</span>
      <div class="container" style={{marginTop:100}}>
        <Photo_upload />
        
        <div class="d-flex row mt-3">
          <span>Heading</span>
          
          <input
            className="form-control"
            type="text"
            onChange={(e) => {
              set_fac_heading( e.target.value);
            }}
            value={fac_heading}
            placeholder=""
          />
        </div>

        <div class="d-flex row mt-3">
          <span>Descriptions</span>
          <input
            className="form-control"
            type="text"
            onChange={(e) => {
              set_fac_description( e.target.value );
            }}
            value={fac_description}
            placeholder=""
          />
        </div>
        <div class="d-flex row mt-3">
          <span>Image</span>
          <input
            type="file"
            
            onChange={(e) => {
              set_file( e.target.files[0] );
            }}
            
           
          />
        </div>
        <br></br>
        <div className="container text-center mb-5">
      <button class="btn btn-primary text-center" onClick={()=>{addFacilities()}}>Save Facilities</button>
      </div>


        <div class="card-box mt-5">
              <span class="heading text-primary ml-1">Facilities</span>
              <hr />

              <div class="groupFacilty">
                <div class="groupFacilty-columns">
                 
                  {
                    fac.map((facility) => {
                      return(
                        <div class="groupbox">
                            <h4>
                              <img src={facility.images} alt="Common areas" />{" "}
                              {facility.heading} <span class="toggle-icon"></span>
                            </h4>
                            <ul>
                              {" "}
                                {facility.description==undefined?null: facility.description.split(",").map((data)=>{
                                  return<li>{data}</li>
                                })}
                            </ul>
                            <div style={{display:"flex",justifyContent:"space-around"}}>
                              <button type="button" class=" btn btn-outline-primary" data-toggle="modal" data-target="#exampleModal" onClick={() => {HandleClick(facility)}}>
                                Edit
                              </button>
                              
                              <button class=" btn btn-outline-primary" onClick={() => {delFacilities(facility.id)}} >Delete</button>
                            </div>

                            {/* <!-- Modal --> */}
                                <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                                  <div class="modal-dialog">
                                    <div class="modal-content">
                                      <div class="modal-header">
                                        <h5 class="modal-title" id="exampleModalLabel">Edit Facilities</h5>
                                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                          <span aria-hidden="true">&times;</span>
                                        </button>
                                      </div>
                                      <div class="modal-body">
                                      <div class="d-flex row mt-3">
                                          <span>Heading</span>
                                          
                                          <input
                                            className="form-control"
                                            type="text"
                                            onChange={(e) => {
                                              set_fac_heading( e.target.value);
                                            }}
                                            value={fac_heading}
                                            placeholder=""
                                          />
                                        </div>

                                        <div class="d-flex row mt-3">
                                          <span>Descriptions</span>
                                          <input
                                            className="form-control"
                                            type="text"
                                            onChange={(e) => {
                                              set_fac_description( e.target.value );
                                            }}
                                            value={fac_description}
                                            placeholder=""
                                          />
                                        </div>
                                        <div class="d-flex row mt-3">
                                          <span>Image</span>
                                          <img src={file} style={{width:"50px",height:"50px"}}></img><br></br>
                                          <input
                                            type="file"
                                            
                                            onChange={(e) => {
                                              set_file( e.target.files[0] );
                                            }}
                                            
                                          
                                          />
                                        </div>

                                      </div>
                                      <div class="modal-footer">
                                        
                                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">Close</button>
                                        <button class=" btn btn-outline-primary" onClick={() => {updateFacilities(fac_id)}} >Save</button>
                                        {/* <button type="button" class="btn btn-primary">Save changes</button> */}
                                      </div>
                                    </div>
                                  </div>
                                </div>
                            {/* <!-- Modal End --> */}
                        </div>
                            
                      )
                    })
                  }
                 </div>
                 </div>
                 </div>
</div>

      {/* about */}

      <div class="container mt-5">
          <span>About Us</span>
        <textarea
          className="form-control"
          style={{ minHeight: 200 }}
          value={about}
          onChange={(e) => {
            set_about(e.target.value);
          }}
        />
      </div>
      <div className="container card-box mt-5 mb-5">
        <div>
          <span class="detail_text">{about}</span>
        </div>
      </div>

      <div className="container text-center mb-5">
      <button class="btn btn-primary text-center" onClick={()=>{save()}}>Save and Launch</button>
      </div>

      {/* contact */}

      <div class="container mt-5">
          <span>Contact us</span><br></br>
          <span>Phone</span>
        <input
          className="form-control"
          // style={{ minHeight: 200 }}
          value={phone}
          onChange={(e) => {
            set_phone(e.target.value);
          }}
        />
        <span>Fax</span>
        <input
          className="form-control"
          // style={{ minHeight: 200 }}
          value={fax}
          onChange={(e) => {
            set_fax(e.target.value);
          }}
        />
        <span>Email</span>
        <input
          className="form-control"
          // style={{ minHeight: 200 }}
          value={email}
          onChange={(e) => {
            set_email(e.target.value);
          }}
        />
      </div>
      <div className="container card-box mt-5 mb-5">
        <div>
        <table>
                              <tr>
                                <td>Phone &nbsp;&nbsp;&nbsp; #{phone}</td>
                                {/* <td>Fax &nbsp;&nbsp;&nbsp; # 217 892 4318</td> */}
                                </tr>
                                <tr>
                                <td>Fax &nbsp;&nbsp;&nbsp; #{fax}</td>
                                </tr>
                                <tr>
                                  <td>Email &nbsp;&nbsp;&nbsp;&nbsp; {email}</td>
                                </tr>

                                {/* Name of website 
                                Email : FirstHeritageInnrantoul@gmail.com */}
                            </table>
        </div>
      </div>


      <div className="container text-center mb-5">

      <button class="btn btn-primary text-center" onClick={()=>{updateContact()}}>Save Contact</button>
      </div>

      {/* faq */}

      <div class="container mt-5">
          <span>FAQ</span><br></br>
          <span>Question</span>
        <textarea
          className="form-control"
          style={{ minHeight: 100 }}
          value={question}
          onChange={(e) => {
            set_question(e.target.value);
          }}
        />
         <span>Answer</span>
        <textarea
          className="form-control"
          style={{ minHeight: 100 }}
          value={answer}
          onChange={(e) => {
            set_answer(e.target.value);
          }}
        />
      </div>
      <div className="container card-box mt-5 mb-5">
        <div>
          <span class="detail_text">Question:</span>
          <span class="detail_text">{question}</span>
        </div>
        <div>
          <span class="detail_text">Answer:</span>
          <span class="detail_text">{answer}</span>
        </div>
      </div>
           <div className="container text-center mb-5">
               <button class="btn btn-primary text-center" onClick={()=>{addFaq()}}>Save FAQ</button>
           </div>

      <div class="card-box mt-5">
              <span class="text-primary heading">
                FAQ About First Heritage Inn
              </span>
              <hr />
                  {
                    faq.map((datafaq) => {
                      return(
                            <div class="card-box my-3">
                              <strong>
                              {datafaq.question}
                                
                              </strong>
                              <br />
                              <span>
                              {datafaq.answer}
                                
                              </span>
                              <br></br>
                              <button class="btn btn-outline-primary" onClick={() => {delFaq(datafaq.id)}}>Delete</button>
                            </div>
                      );
                    })
                  }
             
            </div>

            {/* policy */}

            <div class="container mt-5">
              <span>Policies</span><br></br>
              <span>Heading</span>
            <input
              className="form-control"
              value={heading}
              onChange={(e) => {
                set_heading(e.target.value);
              }}
            />
            <span>Description</span>
            <textarea
              className="form-control"
              style={{ minHeight: 100 }}
              value={description}
              onChange={(e) => {
                set_description(e.target.value);
              }}
            />
          </div>
           <br></br>
          <div className="container text-center mb-5">
               <button class="btn btn-primary text-center" onClick={()=>{addPolicy()}}>Save Policy</button>
           </div>

     
            <div id="policy" class="card-box mt-5">
              <span class="heading text-primary">
                First Heritage Inn Policy
              </span>
              <hr />
              {
                pol.map((datapo) => {
                  return( 
                   
                        <span class="detail_text">
                          <span class="heading_sub">{datapo.heading}</span>
                          <br />
                          <br />
                          <p>{datapo.description}</p>
                          <button class="btn btn-outline-primary" onClick={() => {delPolicy(datapo.id)}} >Delete</button>
                          <br />
                          <br />
                          </span>
                   
                          
                  );
                })
              }
              </div>
              
              <br></br>
           {/* amenities */}

           <div class="container mt-5">
              <span>Rooms Amenities</span><br></br>
              <span>Amenities</span>
            <input
              className="form-control"
              value={amenities}
              onChange={(e) => {
                set_amenities(e.target.value);
              }}
            />
           
          </div>
          <br></br>
          <div className="container text-center mb-5">
               <button class="btn btn-primary text-center" onClick={()=>{addAmenities()}}>Save Amenities</button>
           </div>
          <br></br>
              <div>
                <span class="heading text-primary ml-1">Rooms Amenities</span>
                <hr />
                <table style={{width:"100%"}}>
                      <tr>
                        <th style={{border:"1px solid #dddddd"}}>Amenities</th>
                        <th style={{border:"1px solid #dddddd"}}> Delete</th>
                      </tr>
                {
                  ame.map((data) => {
                    return(
                     
                      <tr>
                        <td style={{border:"1px solid #dddddd"}}>
                        {data.amenities}
                        
                      </td>
                      <td style={{border:"1px solid #dddddd"}}>
                       
                        <button class="btn btn-outline-primary" style={{marginLeft:"100px"}} onClick={() => {delAmenities(data.id)}}>Delete</button>
                      </td>
                      
                      </tr>
                      
                    );
                  })
                }
                </table>
                </div>
    </>
  );
}
