import axios from "axios"
import React from "react"
import Moment from "moment";
import swal from "sweetalert"
import $ from "jquery"
import { useHistory } from "react-router";
import {AiOutlineLogout} from "react-icons/ai"



export default function Booking(){
  const history= useHistory()

    const [signin,set_signin]=React.useState(false)
    const [signup,set_signup]=React.useState(false)

    const [data,set_data]=React.useState([])
    React.useEffect(()=>{
        get_booking()
    },[])


    const get_booking=async()=>{
        let res= await axios.get("/user/booking/list",{ headers: { Authorization: "Bearer " + localStorage.getItem("token") }})
        console.log(res)
        set_data(res.data.data)
    }

    const cancel_booking = async(id)=>{
      let res= await axios.post("/booking/cancel",{booking_id:id},{headers: { Authorization: "Bearer " + localStorage.getItem("token") }})
      if(res.status==200){
        swal("booking cancel.")
      }else{
        swal("Error in Cancel Booking.")

      }
      get_booking()
    }



    return (
        <div className="">
                  <div className="bg-primary banner col-lg-12">
        <div className="container-fluid">
          <div className="right-menu">
            <div className="right-submenu">
              {localStorage.getItem("user") == undefined ? null : (
                <span
                  className="text-white mx-2 py-3"
                  onClick={() => {
                    history.push("/Booking");
                  }}
                >
                  My Booking
                </span>
              )}

              {localStorage.getItem("user") == undefined ? (
                <span
                  onClick={() => {
                    set_signin(true);
                  }}
                  className="border border-white text-white p-1"
                >
                  Sign In
                </span>
              ) : (
                <span
                  onClick={() => {
                    // set_signin(true);
                    localStorage.removeItem("user");
                    localStorage.removeItem("token");
                    localStorage.removeItem("user_details");
                    window.location.href="/";
                  }}
                  className="border border-white text-white p-1"
                >
                  {localStorage.getItem("user_details")}<AiOutlineLogout style={{marginLeft:5}} />
                </span>
              )}

              <span
                onClick={() => {
                  set_signup(true);
                }}
                style={{ cursor: "pointer" }}
                className="bg-danger text-white p-1 mx-3"
              >
                Register
              </span>
              {/* <span className="border border-white text-white c-pa">USD</span> */}
            </div>
          </div>
        </div>
      </div>

            <header>
        <nav
          className="navbar navbar-expand-lg navbar-dark bg-warning w-100"
          style={{ height: "100px" }}
        >
          <div className="container">
            <div className="d-flex row col-sm-10 col-md-8 col-lg-3 nav_mobile">
              <a className="navbar-brand text-dark fonts" href="/">
                First Heritage INN
              </a>
              <span className="subherder">
                420 S Murray Rd Rantoul, Illinois 61866
              </span>
            </div>
            {/* <img src="images/logo.png" className="img-m" /> */}
            {/* <a href="/"> */}
              <img onClick={()=>{history.push("/")}} src="images/logo.png" className="img-m img-mm" />
              {/* </a> */}




            <span style={{fontSize:"30px",cursor:"pointer"}} onClick={()=>{$("#mySidenav").toggleClass("nav_full")}}>&#9776;</span>
   


            <div id="mySidenav" class="sidenav">
              <a
                href="javascript:void(0)"
                class="closebtn"
                onClick={()=>{$("#mySidenav").toggleClass("nav_full")}}
              >
                &times;
              </a>
              <ul className="navbar-nav ml-auto">
                <li className="nav-item active">
                  <a className="nav-link text-dark text-main" href="/" onClick={()=>{$("#mySidenav").toggleClass("nav_full")}}>
                    Home
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link text-dark text-main" href="#about" onClick={()=>{$("#mySidenav").toggleClass("nav_full")}}>
                    About
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link text-dark text-main" href="#policy" onClick={()=>{$("#mySidenav").toggleClass("nav_full")}}>
                    Policy
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link text-dark text-main" href="#" onClick={()=>{$("#mySidenav").toggleClass("nav_full")}}>
                    Contact
                  </a>
                </li>
              </ul>
         
            </div>

            
          </div>
        </nav>
      </header>
<div style={{minHeight:window.innerHeight}}>
{data.length==0?<div style={{width:"100%",textAlign:"center",marginTop:50}}><span style={{textAlign:"center"}}>No Booking Yet</span></div>:
        <table className="table table-borderless mt-5" style={{overflow: "scroll",}}>
            <tr className="bg-dark text-white">
                <td>Booking Id</td>
                <td>No of Rooms</td>
                <td>From</td>
                <td>To</td>
                <td>status</td>
                <td>Action</td>
            </tr>
            {data.map((data,i)=>{
                return<tr>

            <td>{data.booking_id}</td>
                <td>{data.no_of_rooms}</td>
                <td>{Moment(data.start_date).format("DD-MM-YYY")}</td>
                <td>{Moment(data.end_date).format("DD-MM-YYYY")}</td>
                <td>{data.status}</td>
                <td className="text-danger" onClick={()=>{cancel_booking(data.booking_id)}}>Cancel</td>

                </tr>
            })}
            
        </table>
        }
        </div>

        <footer id="footer">
        
        <div class="footer_bottom">
          <div class="container">
            <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <p>© Copyright© 2021 - All Rights Reserved</p>
            </div>
            <div class="clearfix"></div>
            <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <div class="disclaimer-footer-txt">
                <a
                  href="javascript:;"
                  data-toggle="modal"
                  data-target="#footerDisclaimerWindow"
                  style={{textAlign:"right",width:"100%",display: "inline-block",fontSize: "11px", color: "inherit"}}
                >
                </a>

                <div
                  class="modal fade"
                  id="footerDisclaimerWindow"
                  tabindex="-1"
                  role="dialog"
                  aria-labelledby="DisclaimerModalTitle"
                  aria-hidden="true"
                >
                  <div class="modal-dialog" role="document">
                    <div class="modal-content">
                      <div
                        class="modal-header"
                        style={{float: "left", width: "100%",background: "#313030"}}
                      >
                        
                        <button
                          type="button"
                          class="close model-close-btn-css"
                          data-dismiss="modal"
                          aria-label="Close"
                        >
                          <span aria-hidden="true">×</span>
                        </button>
                      </div>
                      <div
                        class="modal-body"
                        style={{width: "100%", height:"200px", overflowY:"auto"}}
                      >
                        <p style={{padding: "30px 0 0 0",fontSize: "14px",color: "#333"}}>
                          <strong style={{color: "#000"}}></strong> This
                          is not an official website. The only purpose of this
                          website is to provide information about the property.
                          For any concern or question please{" "}
                          <a href="javascript:;">contact us.</a>
                        </p>
                      </div>
                      <div class="modal-footer"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </footer>
  

        </div>
    )




}